@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap");

.sectionn {
  width: 100%;
  min-height: 100vh;
  background-color: #161616;
  margin-top: 15px;
}
.containerr {
  width: 80%;
  display: block;
  margin: auto;
  padding-top: 100px;
}
.content-section {
  float: left;
  width: 55%;
}
.image-section {
  float: right;
  width: 40%;
}
.image-section img {
  width: 100%;
  height: auto;
}
.content-section .titlee {
  text-transform: uppercase;
  font-size: 28px;
  color: #f5a425;
}
.content-section .contentabout h3 {
  margin-top: 20px;
  color: #fff;
  font-size: 21px;
}
.content-section .contentabout p {
  margin-top: 10px;
  font-family: sans-serif;
  font-size: 18px;
  line-height: 1.5;
  color: #fff;
}
.fullabout {
  margin-top: -14px;
}
.content-section .contentabout .button {
  margin-top: 30px;
}
.content-section .contentabout .button a {
  background-color: #3d3d3d;
  padding: 12px 40px;
  text-decoration: none;
  color: #f5a425;
  font-size: 25px;
  letter-spacing: 1.5px;
}
.content-section .contentabout .button a:hover {
  background-color: #a52a2a;
  color: #fff;
}
.content-section .social {
  margin: 40px 40px;
}
.content-section .social i {
  color: #a52a2a;
  font-size: 30px;
  padding: 0px 10px;
}
.content-section .social i:hover {
  color: #3d3d3d;
}
@media screen and (max-width: 768px) {
  .container {
    width: 80%;
    display: block;
    margin: auto;
    padding-top: 50px;
  }
  .content-section {
    float: none;
    width: 100%;
    display: block;
    margin: auto;
  }
  .image-section {
    float: none;
    width: 100%;
  }
  .image-section img {
    width: 100%;
    height: auto;
    display: block;
    margin: auto;
  }
  .content-section .title {
    text-align: center;
    font-size: 19px;
  }
  .content-section .contentabout .button {
    text-align: center;
  }
  .content-section .contentabout .button a {
    padding: 9px 30px;
  }
  .content-section .social {
    text-align: center;
  }
}
